h1,
h2,
h3,
h4,
h5,
h6 {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none;
  vertical-align: baseline;
}

h1, .heading00 {
  font-weight: normal;
  font-family: "SST W01 Medium", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 3em;
  letter-spacing: -0.04em;
  line-height: 1.125;
  margin-bottom: 18px;
}


h2, .heading01 {
  font-weight: normal;
  font-family: "SST W01 Medium", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 2.375em;
  letter-spacing: -0.04em;
  line-height: 1.15789;
  margin-bottom: 24px;
}


h3, .heading02 {
  font-weight: normal;
  font-family: "SST W01 Medium", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 1.875em;
  letter-spacing: -0.04em;
