}


h6, .heading05 {
  font-weight: normal;
  font-family: "SST W01 Medium", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 1.125em;
  line-height: 1.44444;
  margin-bottom: 8px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: inherit;
  line-height: 1;
}
